<template>
  <div>
    <div class="container-fluid text-center" style="top:25vh;position: relative">
      <h1>Welcome, {{getUsername}}</h1>
      <div class="mx-auto rounded-circle my-3" style="height: 150px;width: 150px;background-position:center;background-size: cover" :style="'background-image: url(' + userInfo.imgURL + ');'">
      </div>
      <button
        type="button"
        class="btn btn-raised btn-info btn-lg"
        @click="startConversation()"
      >Start a conversation</button>
    </div>
    <!-- <modals-container/> -->
  </div>
</template>

<script>
import { mapState } from "vuex";
import modal from "./SearchUserModal";
import { isMobile } from '../utilities';

export default {
  name: "Homepage",
  components: {modal},
  data() {
    return {
      isMobile: isMobile.any()
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.auth.userInfo,
      getUsername: state => state.auth.userInfo.name
    })
  },
  methods: {
    startConversation: function() {
      console.log(this.userInfo);
      this.$modal.show(
        modal,
        {
          userInfo: this.userInfo
        },
        {
          height: "auto",
          width: this.isMobile ? "100%" : "500px"
        },
        {
          "before-close": event => {
            console.log("this will be called before the modal closes");
          }
        }
      );
    },
    hide() {
      this.$modal.hide("view-post");
      history.pushState(null, "", "/");
    }
  },
  mounted() {
    if (!this.userInfo.studentId || !this.userInfo.fullname || !this.userInfo.class) {
      this.$router.push('/profile');
    }
  }
};
</script>

<style>
@media only screen and (max-width: 600px) {
  .post-modal {
    left: 0 !important;
  }
}
</style>



